export default {
	data() {
		return {
			listArr:[]
		}
	},
	onLoad(options) {
		this.getListArr()
	},
	methods: {
		dianhua:function(i){
			console.log(i)
			uni.makePhoneCall({
				phoneNumber:i.tel
			})
		},
		xianshang(item,i){
			let that=this;
			let usurl=that.url1+"/dinghai/order/Pay";
			uni.showModal({
			        title: '是否要进行线上支付',
			        content: "订单ID为："+item.id,
			        cancelText:'是',
			        confirmText:'否',
			        success(res){
						uni.showLoading({
							title:'正在付款中...'
						})
			          if(res.cancel){
						  uni.request({
						  		url: usurl,
						  		header:{
						  			"Content-Type" : "application/x-www-form-urlencoded"
						  		},
						  		method:'POST',
						  		data: {
						  			id:item.id
						  		},
						      success: (res) => {
								  console.log(res)
						  		if(res.statusCode==200){
						  			if(res.data.flag==200){
										that.listArr.splice(i,1)
										uni.showToast({
											title:'支付成功'
										})
									}else if(res.data.flag=="发单人未充值"){
										uni.showToast({
											title:'余额不足',
											icon:"none"
										})
									}
						  		}else{
						  			uni.showToast({
						  				title:"您的网络打了个盹",
						  				icon:"none"
						  			})
						  		}
						      },
						  	fail : function(err){
						  		uni.showToast({
						  			title:"您的网络打了个盹",
						  			icon:"none"
						  		})
						  	}
						  });
			          }else if(res.confirm){
			             console.log("点击了取消");
			          }
			        }
			   })
		},
		yiZhiFu:function(item,i){
			
			let that=this;
			let usurl=that.url1+"/dinghai/order/updateSettlement";
			uni.showModal({
			        title: '是否已完成线下支付',
			        content: "订单ID为："+item.id,
			        cancelText:'是',
			        confirmText:'否',
			        success(res){
			          if(res.cancel){
						  uni.request({
						  		url: usurl,
						  		header:{
						  			"Content-Type" : "application/x-www-form-urlencoded"
						  		},
						  		method:'POST',
						  		data: {
						  			id:item.id
						  		},
						      success: (res) => {
						  		if(res.statusCode==200){
						  			that.listArr.splice(i,1)
						  		}else{
						  			uni.showToast({
						  				title:"您的网络打了个盹",
						  				icon:"none"
						  			})
						  		}
						      },
						  	fail : function(err){
						  		uni.showToast({
						  			title:"您的网络打了个盹",
						  			icon:"none"
						  		})
						  	}
						  });
			          }else if(res.confirm){
			             console.log("点击了取消");
			          }
			        }
			   })
		},
		getListArr:function(){
				let that=this;
				let usurl=that.url1+"/dinghai/order/selectOrder";
				uni.request({
					url: usurl,
					header:{
						"Content-Type" : "application/x-www-form-urlencoded"
					},
					method:'POST',
					data: {
						useruid:that.user.uid
					},
			    success: (res) => {
					if(res.statusCode==200){
						that.listArr=res.data.flag
						console.log(that.listArr)
					}else{
						uni.showToast({
							title:"您的网络打了个盹",
							icon:"none"
						})
					}
			    },
				fail : function(err){
					uni.showToast({
						title:"您的网络打了个盹",
						icon:"none"
					})
				}
			});
		},
		back : function(){
		   uni.navigateBack({
		
		   });
		},
		xiangQing:function(item){
			uni.navigateTo({
				url:"../XiangQing/XiangQing?item="+JSON.stringify(item)
			})
		}
	}
}
